// Imports
import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "#load[data-v-3be9e75f]{margin-top:3%}h1[data-v-3be9e75f],h2[data-v-3be9e75f],h3[data-v-3be9e75f],h4[data-v-3be9e75f],h5[data-v-3be9e75f]{font-family:Sarabun,sans-serif}", "",{"version":3,"sources":["webpack://./resources/js/pages/contents/CalendarSimulations.vue"],"names":[],"mappings":"AAySA,uBACA,aACA,CACA,oGAKA,8BACA","sourcesContent":["<template>\n  <div id=\"main\">\n    <div id=\"header\">\n      <HeaderComponent />\n    </div>\n\n    <div class=\"container-fluid\" id=\"content\">\n      <div id=\"load\" v-if=\"this.loading\" class=\"d-flex align-items-center\">\n        <strong>Loading...</strong>\n        <div\n          class=\"spinner-border ms-auto\"\n          role=\"status\"\n          aria-hidden=\"true\"\n        ></div>\n      </div>\n      <section v-else class=\"home-section\">\n        <div class=\"row\">\n          <div class=\"col-md-7 mb-5\">\n            <h4>\n              <b>ปฎิทิน||ตารางเวลาเรียน => <u>(จำลอง)</u></b>\n            </h4>\n            <hr />\n            <FullCalendar :options=\"calendarOptions\" />\n          </div>\n          <div class=\"col-md-5\">\n            <h4>\n              <b>รายวิชาที่เลือกลงทะเบียน</b\n              ><router-link\n                class=\"btn btn-outline-secondary float-right\"\n                to=\"/learning/course\"\n                >เลือกวิชาเรียน</router-link\n              >\n            </h4>\n            <hr />\n            <table class=\"table table-hover table-bordered\">\n              <thead>\n                <tr>\n                  <th scope=\"col\">#</th>\n                  <th scope=\"col\">รหัสวิชา</th>\n                  <th scope=\"col\">ชื่อวิชา</th>\n                  <th scope=\"col\">เครื่องมือ</th>\n                </tr>\n              </thead>\n              <tbody>\n                <tr v-for=\"data in dataSimulations\" :key=\"data.id\">\n                  <th scope=\"row\">#</th>\n                  <td>\n                    <u>{{ data.subjectId }}</u>\n                  </td>\n                  <td>{{ data.subjectName }}</td>\n                  <td>\n                    <button\n                      class=\"btn btn-danger\"\n                      @click=\"destroySimulation(data.id)\"\n                    >\n                      ลบ\n                    </button>\n                  </td>\n                </tr>\n              </tbody>\n            </table>\n            <div class=\"text-center bg-light p-3\">\n              <span\n                ><p><b>นักเรียนโปรดตรวจสอบรายการลงทะเบียน</b></p></span\n              >\n              <span\n                ><p class=\"text-danger\">\n                  <b><u>เมื่อนักเรียนมั่นใจแล้ว กรุณากดปุ่ม</u></b>\n                </p></span\n              >\n              <button\n                class=\"btn btn-secondary shadow rounded\"\n                @click=\"createRegister\"\n              >\n                ยืนยันการลงทะเบียน\n              </button>\n            </div>\n          </div>\n        </div>\n        <button class=\"btn btn-warning float-left my-3\" @click=\"$router.go(-1)\">\n          กลับ\n        </button>\n      </section>\n    </div>\n\n    <div id=\"footer\">\n      <FooterComponent />\n    </div>\n  </div>\n</template>\n\n<script scoped>\nimport axios from \"axios\";\nimport HeaderComponent from \"../../components/HeaderComponent.vue\";\nimport FooterComponent from \"../../components/FooterComponent.vue\";\nimport \"@fullcalendar/core/vdom\"; // solves problem with Vite\nimport FullCalendar from \"@fullcalendar/vue\";\nimport dayGridPlugin from \"@fullcalendar/daygrid\";\nimport interactionPlugin from \"@fullcalendar/interaction\";\nimport listPlugin from \"@fullcalendar/list\";\nimport timeGridPlugin from \"@fullcalendar/timegrid\";\n\nexport default {\n  name: \"timetables\",\n  components: { HeaderComponent, FooterComponent, FullCalendar },\n  data() {\n    return {\n      calendarOptions: {\n        plugins: [dayGridPlugin, interactionPlugin, listPlugin, timeGridPlugin],\n        initialView: \"dayGridMonth\",\n        selectable: true,\n        dateClick: this.handleDateClick,\n        events: [\n          {\n            title: \"\",\n            start: \"\",\n            end: \"\",\n          },\n        ],\n        eventColor: \"black\",\n        headerToolbar: {\n          left: \"prev,next today\",\n          center: \"title\",\n          right: \"timeGridDay,timeGridWeek,dayGridMonth,listMonth\", //listWeek\n        },\n        locale: \"th\",\n        slotLabelFormat: { hour12: false, hour: \"2-digit\", minute: \"2-digit\" },\n        buttonText: {\n          today: \"วันนี้\",\n          month: \"เดือน\",\n          week: \"สัปดาห์\",\n          day: \"วัน\",\n          list: \"กำหนดการ\",\n        },\n      },\n      loading: true,\n      dataSimulations: [],\n      check_sync_to_subject_id: [],\n      currentCompare: [],\n      sentDataRegister: [],\n    };\n  },\n  mounted() {\n    if (this.$store.state.token !== \"\") {\n      axios\n        .get(\"/api/my_subject_leasson/\" + this.$store.state.user_id)\n        .then((res) => {\n          if (res.data != \"\") {\n            this.DataCalendar();\n          }\n          this.dataSimulation();\n        })\n        .catch((err) => {\n          this.error = \"Error!!\";\n        });\n    } else {\n      //   alert(\"error this.$store.state.token!!\");\n      this.loading = false;\n      this.$router.push(\"/learning/login\");\n    }\n  },\n  created() {\n    this.DataCalendar();\n    this.dataSimulation();\n  },\n  methods: {\n    handleDateClick: function (arg) {\n      alert(\"date click! \" + arg.dateStr);\n    },\n    DataCalendar() {\n      axios\n        .get(\"/api/showdata_time_simulations/\" + this.$store.state.user_id)\n        .then((res) => {\n          //   console.log(res.data);\n          this.currentCompare = [];\n          this.calendarOptions.events = [];\n          for (let i = 0; i < res.data.length; i++) {\n            this.calendarOptions.events[i] = {\n              end: res.data[i].end,\n              start: res.data[i].start,\n              title: res.data[i].title,\n            };\n            this.currentCompare[i] = {\n              end: res.data[i].end,\n              start: res.data[i].start,\n              title: res.data[i].title,\n            };\n          }\n          // console.log(this.currentCompare);\n          //   this.loading = false;\n        })\n        .catch((err) => {\n          this.error = \"Error!!\";\n        });\n    },\n    dataSimulation() {\n      axios\n        .get(\"/api/show_data_simulations/\" + this.$store.state.user_id)\n        .then((res) => {\n          // console.log(res.data);\n          this.dataSimulations = res.data;\n          for (let i = 0; i < res.data.length; i++) {\n            axios\n              .get(\"/api/check_sync_to_subject_id/\" + res.data[i].subject_id)\n              .then((res) => {\n                // console.log(res.data.check_data);\n                this.sentDataRegister = [];\n                for (let i = 0; i < res.data.check_data.length; i++) {\n                  this.sentDataRegister.push({\n                    subject_id: res.data.check_data[i].subject_id,\n                    sync_id: res.data.check_data[i].sync_id,\n                    user_add: this.$store.state.user_id,\n                  });\n                }\n\n                this.check_sync_to_subject_id = [];\n                for (let i = 0; i < res.data.data.length; i++) {\n                  this.check_sync_to_subject_id[i] = {\n                    end: res.data.data[i].end,\n                    start: res.data.data[i].start,\n                    title: res.data.data[i].title,\n                  };\n                this.calendarOptions.events.push(this.check_sync_to_subject_id[i]);\n                }\n              })\n              .catch((error) => {\n                console.log(error);\n              });\n          }\n          this.loading = false;\n        })\n        .catch((err) => {\n          this.error = \"Error!!\";\n        });\n    },\n    destroySimulation(dataDelete) {\n      axios\n        .get(\"/api/destroy_simulation/\" + dataDelete)\n        .then((res) => {\n          this.DataCalendar();\n          this.dataSimulation();\n        })\n        .catch((error) => {\n          console.log(error);\n        });\n    },\n    createRegister() {\n      // console.log(this.sentDataRegister);\n      //   console.log(this.currentCompare);\n      if (this.sentDataRegister[0] != undefined) {\n        if (confirm(\"ยืนยันการลงทะเบียน\")) {\n          for (let i = 0; i < this.sentDataRegister.length; i++) {\n            const config = {\n              headers: {\n                \"content-type\": \"multipart/form-data\",\n                \"X-CSRF-TOKEN\": document.querySelector(\n                  'meta[name=\"csrf-token\"]'\n                ).content,\n              },\n            };\n\n            let formData = new FormData();\n            formData.append(\"user_add\", this.$store.state.user_id);\n            formData.append(\"sync_id\", this.sentDataRegister[i].sync_id);\n            formData.append(\"subject_id\", this.sentDataRegister[i].subject_id);\n\n            axios\n              .post(\"/api/select_subject\", formData, config)\n              .then((res) => {\n                if (res.data.data == true) {\n                  this.checkAlert2();\n                } else {\n                  this.$router.push(\"/learning/dashboard/lessons\");\n                }\n                //   console.log(res.data);\n              })\n              .catch((err) => {\n                this.error = \"Error!!\";\n              });\n          }\n        }\n      } else {\n        alert(\"ยังไม่ได้เลือกวิชาเรียน!!\");\n      }\n    },\n    checkAlert() {\n      alert(\"สามารถเพิ่มได้\");\n    },\n    checkAlert2() {\n      alert(\"เวลาซ้ำซ้อนกัน!!\");\n    },\n  },\n};\n</script>\n\n\n<style scoped>\n#load {\n  margin-top: 3%;\n}\nh1,\nh2,\nh3,\nh4,\nh5 {\n  font-family: \"Sarabun\", sans-serif;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
